import { Divider } from 'antd'
import axios from 'axios'
import React, { useEffect, useRef, useState } from 'react'
import { Button, Card, Col, Container, Row } from 'react-bootstrap'
import { useSelector } from 'react-redux'

const Bills = () => {
  const patient = useSelector((state) => state.loggedInRoleUserData.loggedInRoleUserData)
  const [bills, setBills] = useState([])
  const printRef = useRef(null)

  //eslint-disable-next-line
  const getBills = async () => {
    try {
      const patientId = await patient.uniqueId
      const res = await axios.post('/api/v1/patient/patientBillsHistory', { patientId })
      if (res.data.success) {
        setBills(res.data.data)
      }
    } catch (error) {
      console.log(error)
    }
  }
  useEffect(() => {
    getBills()
  }, [])
  return (
    <>
      <h4>Bills</h4>
      {/* Sample Starts from here */}

      {bills && bills.length > 0 ? (
        bills.map((bill, idx) => (
          <Container ref={printRef} key={idx} fluid as={Card} className="my-3">
            <Row>
              <Card>
                {/* Date Containe */}
                <Container fluid className="d-flex justify-content-between mx-2 my-2">
                  <div
                    style={{
                      fontSize: '1.1rem',
                    }}
                  >
                    <b>Date</b> : {new Date(bill.updatedAt).toLocaleDateString()}
                  </div>
                  <div
                    style={{
                      fontSize: '1.1rem',
                      wordBreak: 'break-word',
                    }}
                  >
                    <b>Bill No.</b> :{bill._id}
                  </div>
                </Container>
                <Divider className="my-1" />

                {/* Prescription Containe */}
                <Container fluid>
                  <Row className="d-flex justify-content-between">
                    <div className="col-12 col-lg-4 col-sm-5">
                      <h5>Service</h5>
                      Visited {bill.department} department
                    </div>
                    <div className="col-12 col-lg-4 col-sm-5">
                      <h5>Department</h5>
                      {bill.department}
                    </div>
                    <div className="col-12 col-lg-3 offset-lg-1 col-sm-5 offset-1 offset-sm-7 sm-my-5">
                      <b>Paid By :</b> {bill.paymentMode}
                    </div>
                  </Row>
                  <Container className="d-flex justify-content-end align-items-center flex-column flex-sm-column flex-md-row flex-lg-row">
                    {/* <Button className="m-2 bg-black text-white border-0 col-lg-2 col-md-2 col-12">Download</Button> */}
                    <Button
                      className="m-2 bg-black text-white border-0 col-lg-2 col-md-2 col-12"
                      style={{
                        whiteSpace: 'nowrap',
                        // fontFamily: 'Montserrat',
                      }}
                      onClick={() => {
                        const printWindow = window.open('', '_blank')
                        printWindow.document.write('<html><head><title>Aman Hospital</title>')
                        printWindow.document.write(
                          '<style>@media print { @page { size: 148mm 100mm; margin: 0; } } *{ font-family: Montserrat;}</style>',
                        )
                        printWindow.document.write('</head><body>')

                        // Update the content to include hospital name and contact number
                        printWindow.document.write('<h1>Aman Hospital</h1>')
                        printWindow.document.write('<p>Contact: 9876543210</p>')

                        printWindow.document.write(printRef.current.innerHTML)
                        printWindow.document.write('</body></html>')
                        printWindow.document.close()
                        printWindow.print()
                      }}
                    >
                      Download
                    </Button>
                  </Container>
                </Container>
              </Card>
            </Row>
          </Container>
        ))
      ) : (
        <>
          <Container>No data Found for Bills</Container>
        </>
      )}
    </>
  )
}

export default Bills
